var loginRes, _self;
export default {
	data() {
		return {
			
		}
	},
	onLoad() {
		_self = this;
		loginRes = this.checkLogin('../tabbar-4/tabbar-4', 2);
		if(!loginRes){return ;}
		
	},
	onShow() {
		loginRes = this.checkLogin('../tabbar-4/tabbar-4', 2);
		if(!loginRes){return false;}
		
	},
	methods: {
		SetActive(e) {
			this.active = e.detail.value
		}
	}
}
